.feedback-form {
  position: relative;

  min-width: 550px;

  @include vp-1023 {
    min-width: 100%;
  }

  & .invalid {
    border-right: 15px solid $color-torch-red-transparent-70;
  }
}

.feedback-form h3 {
  margin: 0;
  margin-bottom: 12px;

  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  font-family: $ff-roboto;
  color: $color-default-white;

  @include vp-1023 {
    margin-bottom: 5px;
  }
  @include vp-767 {
    margin-bottom: 5px;

    font-size: 30px;
    line-height: 42px;
  }
}

.feedback-form small {
  display: block;
  margin: 0;
  margin-bottom: 35px;
  padding-left: 5px;

  font-size: 16px;
  line-height: 22px;

  @include vp-1023 {
    margin-bottom: 25px;
    padding-left: 0;
  }
  @include vp-767 {
    margin-bottom: 34px;

    font-size: 14px;
    line-height: 19px;
  }
}

.feedback-form__wrapper-checkbox {
  position: relative;

  display: flex;
  justify-content: flex-end;
  order: 5;
  max-width: 260px;
  padding-left: 22px;

  @include vp-1023 {
    margin-right: 60px;
  }
  @include vp-767 {
    margin-right: 0;
    padding-left: 31px;
  }
}

.feedback-form small,
.feedback-form label,
.feedback-form input,
.feedback-form textarea,
.feedback-form input::-webkit-input-placeholder,
.feedback-form textarea::-webkit-input-placeholder {
  line-height: 22px;
  color: $color-default-white;
}

.feedback-form form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  max-width: 585px;
  padding-left: 5px;

  @include vp-1023 {
    max-width: 100%;
    padding-left: 0;
  }
}

.feedback-form input {
  width: 47.45%;
  max-width: 275;
  margin-bottom: 20px;
  padding: 15px;

  @include vp-1023 {
    padding: 14px;
  }
  @include vp-767 {
    flex-basis: 100%;
  }
}

.feedback-form input:hover,
.feedback-form input:focus,
.feedback-form textarea:hover,
.feedback-form textarea:focus {
  outline: 1px solid $color-black-pearl-transparent-15;
  opacity: 0.8;
}

.feedback-form textarea {
  flex-basis: 100%;
  min-height: 155px;
  margin-bottom: 35px;
  padding: 21px 15px;

  @include vp-1023 {
    padding: 30px 15px;
  }
  @include vp-767 {
    min-height: 180px;
    margin-bottom: 32px;
    padding: 23px 15px;
  }
}

.feedback-form input,
.feedback-form textarea {
  background-color: $color-black-pearl-transparent-15;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.feedback-form input[type="checkbox"] {
  top: 15px;
  left: 0;
}

.feedback-form label {
  font-size: 14px;
  line-height: 16px;

  cursor: pointer;
  opacity: 0.8;
}

.feedback-form input[type="checkbox"] + label::before {
  content: "";
  position: absolute;
  top: 7px;
  left: -5px;

  width: 15px;
  height: 15px;

  border: 1px solid $color-default-white;
  border-radius: 2px;

  @include vp-1023 {
    top: 5px;
    left: 0;
  }
  @include vp-767 {
    top: 6px;
    left: 5px;
  }
}

.feedback-form input[type="checkbox"]:checked + label::after {
  content: "";
  position: absolute;
  top: 12px;
  left: -3px;

  width: 10px;
  height: 8px;

  background: url("../img/sprite/icon-check.svg") no-repeat;

  @include vp-1023 {
    top: 9px;
    left: 2px;
  }
  @include vp-767 {
    top: 9px;
    left: 7px;
  }
}

.feedback-form input[type="checkbox"] + label:hover,
.feedback-form input[type="checkbox"] + label:focus,
.feedback-form input[type="checkbox"]:focus + label {
  opacity: 0.6;
}

.feedback-form input[type="checkbox"]:hover + label::before,
.feedback-form input[type="checkbox"]:focus + label::before {
  border: 1 solid $color-default-white;
  opacity: 0.6;
}

.feedback-form button {
  justify-content: center;
  order: 4;
  min-width: 265px;
  margin: 0;
  padding: 13px 25px;

  line-height: 19px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.05em;

  border-radius: 3px;

  @include vp-1023 {
    margin-right: 10px;
  }
  @include vp-767 {
    flex-basis: 100%;
    margin-right: 0;
    margin-bottom: 25px;
  }
}
